<template>
  <div>
    <div class="panel-footer operate">
      <div class="pull-left hidden-xs">
        <div class="social-share-cs "></div>
      </div>

      <div class="pull-right actions">
        <a href="javascript:void(0);" class="admin popover-with-html" data-content="">
          <i class="fa fa-trophy"></i>
        </a>

        <a href="javascript:void(0);"
           class="admin popover-with-html" data-content="">
          <i class="fa fa-thumb-tack"></i>
        </a>

        <a @click="deleteBlog()" href="javascript:void(0);" class="admin popover-with-html "
           data-content="">
          <i class="fa fa-anchor"></i>
        </a>

        <a @click="deleteBlog()" href="javascript:void(0);" class="admin">
          <i class="fa fa-trash-o"></i>
        </a>

        <router-link
          :to="{ name: 'blog_edit', params: { id: $route.params.id }}" class="admin">
          <i class="fa fa-pencil-square-o"></i>
        </router-link>

        <a href="javascript:void(0);" class="admin popover-with-html" data-toggle="modal"
           data-target="#exampleModal" data-content="">
          <i class="fa fa-plus"></i>
        </a>
      </div>
      <div class="clearfix"></div>
    </div>

    <div class="modal fade" id="exampleModal" tabindex="-1" role="" aria-labelledby="exampleModalLabel">
      <div class="modal-dialog">
        <div class="modal-content">

          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span
              aria-hidden="true">&times;</span></button>
            <h4 class="modal-title" id="exampleModalLabel"></h4>
          </div>

          <form method="POST" action="" accept-charset="UTF-8">
            <div class="modal-body">

              <div class="alert alert-warning">
                , , .
              </div>

              <div class="form-group">
                <textarea
                  class="form-control" style="min-height:20px"
                  placeholder="markdown" name="content" cols="50"
                  rows="10"></textarea>
              </div>

            </div>

            <div class="modal-footer">
              <button type="button" class="btn btn-default" data-dismiss="modal"></button>
              <button type="submit" class="btn btn-primary"></button>
            </div>

          </form>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import swal from 'sweetalert'
  import bus from '../../bus/eventbus'

  export default {
    mounted () {},
    methods: {
      deleteBlog () {
        var that = this
        swal({
          title: '',
          text: '',
          type: 'warning',
          showCancelButton: true,
          cancelButtonText: '',
          confirmButtonText: ''
        }, function () {
          that.$store.dispatch('deleteBlog', that.$route.params.id).then((data) => {
            that.$router.replace('/')
            bus.$emit('toast', '')
          })
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../../node_modules/sweetalert/dist/sweetalert.css";

  .topic .panel-footer {
    background-color: #fff;
    border: 1px solid #ddd;
    border-top: none;
  }

  .topic .panel-footer {
    border: none;
  }

  .topic .panel-footer {
    padding-top: 0px;
  }

  .meta a, .operate a {
    padding: 1px 2px;
    color: #B9BDC0;
    text-decoration: none;
  }

  .operate a.admin {
    display: inline-block;
    margin-left: 11px;
  }

  .btn-primary {
    color: #fff;
    background-color: #00b5ad;
    border-color: #00b5ad;
  }
</style>



