import { Validator } from 'vee-validate'

const dict = {
  zh_CN: {
    custom: {
      name: {
        required: ''
      },
      email: {
        required: '',
        email: ''
      },
      password: {
        required: '',
        min: '6'
      },
      password_confirmation: {
        confirmed: '',
        required: ''
      }
    }
  }
}

Validator.updateDictionary(dict)



